import { useForm } from "react-hook-form"
import { useEffect, useState } from "react";
import c from './CallbackForm.module.css';
// import { sendOrder } from "../../utils/SendOrder";

export const CallbackForm = ({ outerHandler }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isValid },
  } = useForm({
    mode: 'onChange',
  })

  const [ isSubmitted, setIsSubmitted ] = useState(false);

  useEffect(() => {
    let isSubmittedSession = sessionStorage.getItem('submitted');
    if (isSubmittedSession === 'true') {
      setIsSubmitted(true)
    }
  }, []);
  
  const onSubmit = (data) => {
    let message = `   \n : ${data.name}\n : ${data.phone}\n`;
    
    // sessionStorage.setItem('submitted', 'true');
    console.log(message)
    setIsSubmitted(true);
    // outerHandler && outerHandler();
    // sendOrder({message});
    // reset();
  }

  return (
  <div className={c.callback}>
    {
      isSubmitted ? (
        <div className={c.callback__submitCover}>
          <h2 className={c.callback__submitTitle}> !</h2>
          <p className={c.callback__submitSubtitle}>  </p>
        </div>
      )
    : <>
      <div className={c.formHeader}>
        <p className={c.formHeader__title}> 10 %   </p>
        <p className={c.formHeader__subtitle}>   </p>
      </div>
      <form 
        onSubmit={handleSubmit(onSubmit)}
        className={c.form}
        autoComplete='false'
      >
        <label>
          <input
          {...register("name", { 
            required: ' ', 
            pattern: {
              value: /^[a-zA-Z--\s]+$/,
              message: '     '
            },
            minLength: {
              value: 2,
              message: ' 2 '
            },
            maxLength: {
              value: 30,
              message: ' 30 '
            }
          })}
            placeholder=' *'
          />
          {errors?.name && <span className={c.form__error}>{errors?.name?.message || ' '}</span>}
        </label>

        <label>
          <input
            {...register("phone", { 
              required: ' ',
              minLength: {
                value: 7,
                message: ' 7 '
              },
              maxLength: {
                value: 30,
                message: ' 30 '
              }
            })}
              placeholder='  E-mail*'
            />
          {errors?.phone && <span className={c.form__error}>{errors?.phone?.message || ' '}</span>}
        </label>

        <label htmlFor="callbackAgreement" className={c.form__agreementLabel}>
          <input 
            type="checkbox"
            id='callbackAgreement'
            {...register("callbackAgreement", { 
              required: ' ',
            }
          )} />
              
          {errors?.callbackAgreement && <span className={c.form__error}>{errors?.callbackAgreement?.message || ' '}</span>}
        </label>

        <input type="submit" disabled={!isValid} />
      </form>
    </>
    }
  </div>
  )
}